import * as React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';
import { Container, Row, Col, Button, ButtonGroup } from 'react-bootstrap';
import CoffeeCard from '../../components/CoffeeCard'

const dummyPrice = 65

export default function PosPage() {
    let items = []
    let [coffeeTitles, setCoffeeTitles] = React.useState([])
    let [subMenu, setSubMenu] = React.useState('hot')

    React.useEffect(async () => {
        let res = await fetch(`https://api.sampleapis.com/coffee/${subMenu}`)
        let coffees = await res.json()
            
        for (let i = 0; i < coffees.length; i++) {
            items.push (
                <CoffeeCard
                    image = {coffees[i].image}
                    title = {coffees[i].title }
                    description = {coffees[i].description}
                    price = {dummyPrice}
                />
            )
        }
        setCoffeeTitles(items)
    }, [subMenu])

    return (
        <>
            <h1>Coffee</h1>
            <ButtonGroup>
                <Button variant="secondary">Hot</Button>
                <Button variant="secondary" onClick={() => setSubMenu('iced')}>Cold</Button>
            </ButtonGroup>
            <Row>
                {coffeeTitles}
            </Row>
        </>
    )
}